import logo from './../../logo.svg';
import './Login.css';
import React, { useRef } from "react";
import { useNavigate } from "react-router-dom";
import { useToast } from '@chakra-ui/react'

const Login = () => {

  document.title = 'XSeed App'
  const idInputRef = useRef();
  const passInputRef = useRef();

  const toast = useToast()

  let navigate = useNavigate(); 

  const handleKeypress = e => {
    //it triggers by pressing the enter key
    if (e.keyCode === 13) {
      signIn();
    }
  };

  const signIn = () => {

    if(idInputRef?.current?.value.trim() === 'john' && passInputRef?.current?.value.trim() === '123456'){
      console.log("sign in success")
      sessionStorage.setItem("user", idInputRef?.current?.value);
      navigate('chapterselect')
    } else {
      toast({
        title: 'Error',
        description: "Incorrect ID or Password.",
        status: 'error',
        duration: 2000,
        isClosable: true,
      })
    }

  }
  
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p style={{marginBottom : '40px'}}>
          Login to XSeed
        </p>
        <input style={{marginBottom : '20px', color : '#000000', paddingLeft : '10px'}} ref={idInputRef} type='text' placeholder="ID"/>
        <input onKeyDown={handleKeypress} style={{marginBottom : '20px', color : '#000000', paddingLeft : '10px'}} ref={passInputRef} type='password' placeholder="Password"/>
        <button style={{marginTop : '20px'}} onClick={signIn}>Sign In</button>
      </header>
    </div>
  );
}

export default Login;
